@import url(https://fonts.googleapis.com/css2?family=Mali&family=Russo+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}


.header-h1{
  font-family: 'Russo One', sans-serif;
  font-size: 300%;
  text-align: center;
  color: #001233;
  text-shadow: 2px 0 0 #0353A4, -2px 0 0 #0353A4, 0 2px 0 #0353A4, 0 -2px 0 #0353A4
  
}

.portifolio-header{
  display: flex;
  flex-direction: column;
  background-color: #001845;
  background-image: url(/static/media/Texture-Circuit-Board-Pcb-Background-Wallpaper-5962474.21435df7.png);
  margin-bottom: 2vh;

}

.header-nav{
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  text-align: center;
}

.nav-button{
  background-color: #00123391;
  flex-grow: 1;
  border:dotted black 1px;
  padding: 1vh;
  text-decoration: none;
  font-family: 'Mali', cursive;
  color:#0353A4;
  font-size: 2.5vh;
}

.nav-button:hover{
  background-color: #0353A4;
  color:#00123391;
}


.footer-nav{
  display: flex;
  justify-content: space-around;
}

.footer-nav a{
  display: flex;
  align-items: center;
}

.footer-icon{
  width: 20vh;
}

.footer-icon:hover{
  opacity: 0.8;
}

.portfolio-footer{
  background-color: #0353A4;
  background-image: url(/static/media/45-degree-fabric-dark.3a1471f2.png);
}


